/* http://meyerweb.com/eric/tools/css/reset/
   v5.0.1 | 20191019
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, menu, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
main, menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, main, menu, nav, section {
  display: block;
}

/* HTML5 hidden-attribute fix for newer browsers */
*[hidden] {
  display: none;
}

body {
  line-height: 1;
}

menu, ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
}

* {
  box-sizing: border-box;
}

.srOnly {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
}

.heading {
  margin: 0;
  font: 600 2rem/150% Poppins, sans-serif;
  text-transform: capitalize;
}
@media (min-width: 48rem) {
  .heading {
    font-size: 2.4rem;
  }
}
@media (min-width: 75rem) {
  .heading {
    font-size: 2.8rem;
  }
}
.heading-lg {
  font-size: 2.4rem;
}
@media (min-width: 48rem) {
  .heading-lg {
    font-size: 3.2rem;
  }
}
@media (min-width: 75rem) {
  .heading-lg {
    font-size: 4rem;
  }
}

.text {
  margin: 0;
  font: 400 1.6rem/150% Open Sans, sans-serif;
}
@media (min-width: 75rem) {
  .text {
    font-size: 1.8rem;
  }
}
.text-link {
  color: inherit;
  text-decoration: none;
}
.text-link:focus {
  outline: 0.1rem solid #ff52bf;
}
.text-link:hover {
  text-decoration: underline;
}

.colorLight {
  color: #ebfbff;
}

.colorDark {
  color: #00252e;
}

.colorGray {
  color: #808d99;
}

.logo {
  width: 10.4rem;
  height: 1.6rem;
  background: url(logo.svg) left center no-repeat;
  background-size: 100% 100%;
}
@media (min-width: 48rem) {
  .logo {
    width: 15.2rem;
    height: 2.4rem;
  }
}
@media (min-width: 75rem) {
  .logo {
    width: 20rem;
    height: 3.2rem;
  }
}
.logo-light {
  background: white;
  -webkit-mask: url(logo.svg) no-repeat center/contain;
}
.logo-lg {
  width: 16.8rem;
  height: 2.7rem;
}
@media (min-width: 75rem) {
  .logo-lg {
    width: 24rem;
    height: 4rem;
  }
}

.button {
  padding: 0.3em 2.1em 0.5em;
  color: #00252e;
  font-size: 1rem;
  text-transform: capitalize;
  background: white;
  border: 0.1em solid white;
  border-radius: 1.1em;
  box-shadow: 0 0.4em 0.6em rgba(0, 0, 0, 0.1);
  cursor: pointer;
  transition: 0.2s;
}
@media (min-width: 48rem) {
  .button {
    padding: 0.9em 3.1em 1.3em;
    border-radius: 2em;
  }
}
@media (min-width: 75rem) {
  .button {
    padding: 1.2em 6.3em 1.5em;
    border-radius: 2.4em;
  }
}
.button:focus {
  border: 0.1em solid #00252e;
  outline: none;
}
.button:hover {
  box-shadow: 0 0.5em 0.8em rgba(0, 0, 0, 0.3);
}
.button:active {
  transform: scale(0.98);
  filter: brightness(0.9);
}
.button_label {
  font: 700 1em/140% Open Sans, sans-serif;
}
@media (min-width: 48rem) {
  .button_label {
    font-size: 1.2em;
  }
}
@media (min-width: 75rem) {
  .button_label {
    font-size: 1.4em;
  }
}
.button-accent {
  padding: 0.9em 3.1em 1.3em;
  color: white;
  background: #ff52bf;
  border: 0.1em solid #ff52bf;
  border-radius: 2em;
}
@media (min-width: 75rem) {
  .button-accent {
    padding: 1.6em 3.1em 1.9em;
    border-radius: 2.8em;
  }
}
.button-accent > .button_label {
  font-size: 1.2em;
}
@media (min-width: 75rem) {
  .button-accent > .button_label {
    font-size: 1.4em;
  }
}

.illustration {
  width: 100%;
  padding-bottom: 81.3%;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: contain;
}
@media (min-width: 75rem) {
  .illustration {
    padding-bottom: 80.5%;
  }
}

.paper {
  background: white;
  border-radius: 1.4rem;
  box-shadow: 0 0 1.4rem rgba(0, 0, 0, 0.1);
}

.feature {
  display: grid;
  grid-gap: 3.2rem;
  align-content: start;
  padding: 3.2rem 4rem;
  text-align: center;
}
@media (min-width: 48rem) {
  .feature {
    grid-template-columns: 1fr 1fr;
    align-items: center;
    text-align: left;
  }
}
@media (min-width: 75rem) {
  .feature {
    grid-gap: 16.6rem;
    grid-template-columns: auto auto;
    padding: 5.2rem 2.6rem 5.2rem 12rem;
  }
}
.feature_image {
  padding: 2.4rem 0.8rem;
}
@media (min-width: 48rem) {
  .feature_image {
    padding: 2.4rem 0;
  }
}
@media (min-width: 75rem) {
  .feature_image {
    width: 41.8rem;
    padding: 0;
  }
}
.feature_content {
  display: grid;
  grid-gap: 1.4rem;
}
@media (min-width: 75rem) {
  .feature_content {
    grid-gap: 1.2rem;
  }
}
@media (max-width: 47.9375rem) {
  .feature_text {
    font-size: 1.4rem;
  }
}

.contacts {
  padding-left: 4rem;
}
.contacts_list {
  display: grid;
  grid-gap: 2.4rem;
  margin: 0;
  padding: 0;
  list-style: none;
}
.contacts_list :nth-child(1)::before {
  background: url(icon-location.svg) left center no-repeat;
  background-size: contain;
}
.contacts_list :nth-child(2)::before {
  background: url(icon-phone.svg) left center no-repeat;
  background-size: contain;
}
.contacts_list :nth-child(3)::before {
  background: url(icon-email.svg) left center no-repeat;
  background-size: contain;
}
.contacts_item {
  position: relative;
  padding-right: 0.1rem;
}
.contacts_item::before {
  position: absolute;
  top: 0.3rem;
  left: -3.8rem;
  display: block;
  width: 2rem;
  height: 2rem;
  content: "";
}

.nav {
  justify-self: stretch;
}
.nav_list {
  display: grid;
  grid-gap: 1.6rem;
  align-items: start;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 75rem) {
  .nav_list {
    grid-auto-flow: column;
    grid-row-gap: 1.6rem;
    grid-column-gap: 28.57%;
    grid-template: repeat(3, auto)/repeat(2, 1fr);
  }
}
.nav_link {
  font-size: 1.8rem;
}

.social {
  display: grid;
  grid-gap: 1.2em;
  grid-template-columns: repeat(3, auto);
  justify-content: center;
  margin: 0;
  padding: 0;
  font-size: 1rem;
  list-style: none;
}
.social_item {
  display: block;
}
.social_link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 3.2em;
  height: 3.2em;
  color: white;
  background: transparent;
  border-radius: 50%;
  box-shadow: inset 0 0 0 0.1em white;
  transition: 0.2s;
}
.social_link:focus, .social_link:hover {
  color: #00252e;
  background: white;
  outline: none;
}

.icon {
  width: 50%;
  height: 50%;
}

.copyright {
  margin: 0;
  font: 600 1.2rem/140% Poppins, sans-serif;
  letter-spacing: -0.01em;
  text-align: center;
}

.content {
  position: relative;
}

.header {
  position: absolute;
  top: 0;
  z-index: 1;
  width: 100%;
  padding: 2.4rem 1.6rem 0;
}
@media (min-width: 48rem) {
  .header {
    padding: 3.2rem 3.2rem 0;
  }
}
@media (min-width: 75rem) {
  .header {
    padding: 4.8rem 8rem 0;
  }
}
.header_container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (min-width: 75rem) {
  .header_container {
    max-width: 128rem;
    margin: 0 auto;
  }
}

.hero {
  position: relative;
  padding: 13rem 3.6rem 3.3rem;
  background: url(bg-hero-mobile.svg) top center no-repeat, #ebfbff;
  background-size: 100%;
}
@media (min-width: 48rem) {
  .hero {
    padding-bottom: 5rem;
  }
}
@media (min-width: 64rem) {
  .hero {
    padding: 16rem 8rem 6.6rem;
    background-image: url(bg-hero-desktop.svg);
  }
}
.hero_container {
  display: grid;
  grid-gap: 5.2rem;
}
@media (min-width: 48rem) {
  .hero_container {
    max-width: 54rem;
    margin: 0 auto;
  }
}
@media (min-width: 64rem) {
  .hero_container {
    grid-gap: 3.2rem;
    grid-template-columns: 1fr 1fr;
    max-width: unset;
  }
}
@media (min-width: 75rem) {
  .hero_container {
    grid-gap: 6.4rem;
    grid-template-columns: 65fr 87fr;
    align-items: center;
    max-width: 128rem;
  }
}
.hero_content {
  text-align: center;
}
@media (min-width: 48rem) {
  .hero_content {
    padding: 0 7.8rem;
  }
}
@media (min-width: 64rem) {
  .hero_content {
    padding: 0;
    text-align: left;
  }
}
@media (min-width: 75rem) {
  .hero_content {
    margin-top: 1.4rem;
  }
}
.hero_content :nth-child(2) {
  margin-top: 2.2rem;
}
.hero_content :nth-child(3) {
  margin-top: 3.2rem;
}
@media (min-width: 75rem) {
  .hero_content :not(:first-child) {
    margin-top: 3rem;
  }
}
.hero_button {
  width: 24rem;
}
@media (min-width: 75rem) {
  .hero_button {
    width: 28rem;
  }
}
.hero_image {
  padding-bottom: 71.3%;
  background-image: url(illustration-mockups.svg);
}

.features {
  padding: 6rem 2rem 23rem;
  background: white;
}
@media (min-width: 48rem) {
  .features {
    padding-top: 10rem;
  }
}
@media (min-width: 64rem) {
  .features {
    padding: 16rem 10.4rem 34.3rem;
  }
}
.features_container {
  display: grid;
  grid-auto-rows: 1fr;
  grid-gap: 4rem;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 48rem) {
  .features_container {
    max-width: 123.2rem;
    margin: 0 auto;
  }
  .features_container :nth-child(odd) .feature_image {
    order: 1;
  }
}
.features_container :nth-child(1) .illustration {
  background-image: url(illustration-grow-together.svg);
}
.features_container :nth-child(2) .illustration {
  background-image: url(illustration-flowing-conversation.svg);
}
.features_container :nth-child(3) .illustration {
  background-image: url(illustration-your-users.svg);
}

.cta {
  position: absolute;
  bottom: -6.7rem;
  left: 50%;
  width: 35.1rem;
  padding: 4.4rem 1.6rem 4rem;
  text-align: center;
  transform: translateX(-50%);
}
@media (min-width: 48rem) {
  .cta {
    width: 48rem;
    padding: 5rem 3rem;
  }
}
@media (min-width: 75rem) {
  .cta {
    bottom: -9.5rem;
    width: 80rem;
    padding: 6.4rem 10rem 5.2rem;
  }
}
.cta_heading {
  font-size: 1.8rem;
}
@media (min-width: 48rem) {
  .cta_heading {
    font-size: 2.4rem;
  }
}
@media (min-width: 75rem) {
  .cta_heading {
    font-size: 3.2rem;
  }
}
.cta_button {
  width: 20rem;
  margin-top: 2.4rem;
}
@media (max-width: 47.9375rem) {
  .cta_button > .button_label {
    font-size: 1em;
    line-height: 160%;
  }
}
@media (min-width: 48rem) {
  .cta_button {
    width: 24rem;
    margin-top: 3rem;
  }
}
@media (min-width: 75rem) {
  .cta_button {
    width: 40rem;
    margin-top: 3.6rem;
    font-size: 1.4rem;
  }
}

.footer {
  padding: 13.8rem 1.6rem 4.6rem;
  background: #00252e;
}
@media (min-width: 48rem) {
  .footer {
    padding-right: 8rem;
    padding-left: 8rem;
  }
}
@media (min-width: 64rem) {
  .footer {
    padding: 19rem 8rem 6.4rem 12rem;
  }
}
@media (min-width: 48rem) {
  .footer_container {
    display: grid;
    grid-row-gap: 2.4rem;
    grid-column-gap: 8rem;
    grid-template: "footer_logo      footer_logo" auto "footer_contacts  footer_nav" auto "footer_social    footer_social" auto "footer_copyright footer_copyright" auto/2fr 1fr;
  }
}
@media (min-width: 64rem) {
  .footer_container {
    grid-template: "footer_logo      footer_logo      footer_logo" auto "footer_contacts  footer_nav       footer_social" auto "footer_copyright footer_copyright footer_copyright" auto/2fr 1fr 1fr;
  }
}
@media (min-width: 75rem) {
  .footer_container {
    grid-row-gap: 4rem;
    grid-column-gap: 9.68%;
    grid-template: "footer_logo      footer_logo      footer_logo" auto "footer_contacts  footer_nav       footer_social" auto "footer_copyright footer_copyright footer_copyright" auto/39fr 35fr 26fr;
    justify-items: start;
    max-width: 124rem;
    margin: 0 auto;
  }
}
.footer_logo {
  grid-area: footer_logo;
}
.footer_contacts {
  grid-area: footer_contacts;
}
@media (max-width: 47.9375rem) {
  .footer_contacts {
    margin-top: 2.4rem;
  }
}
@media (min-width: 75rem) {
  .footer_contacts {
    font-size: 1.6rem;
  }
}
.footer_nav {
  grid-area: footer_nav;
}
@media (max-width: 47.9375rem) {
  .footer_nav {
    margin-top: 5.6rem;
  }
}
.footer_social {
  grid-area: footer_social;
}
@media (max-width: 47.9375rem) {
  .footer_social {
    margin-top: 5.3rem;
  }
}
.footer_copyright {
  grid-area: footer_copyright;
}
@media (max-width: 47.9375rem) {
  .footer_copyright {
    margin-top: 2.4rem;
  }
}
@media (min-width: 64rem) {
  .footer_copyright {
    justify-self: end;
  }
}